*,
body,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
button,
textarea {
  border: none;
  outline: none;
}

button {
  background-color: transparent;
  cursor: pointer;
}

a {
  &:link,
  &:visited,
  &:active {
    text-decoration: none;
  }
}
